
html {
  font-family: "Helvetica Neue", "Luxi Sans", "DejaVu Sans", Tahoma, "Hiragino Sans GB", "Microsoft Yahei", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
body {
  margin: 0;
  background-color: #f2f3f5;
  padding: 80px 0;
}
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
}
.nav-link {
  color: rgba(255, 255, 255, 0.8);
  text-decoration: none;
  line-height: 55px;
  font-weight: 300;
  margin-right: 1.8em;
}
.nav-link:hover {
    color: #fff;
}
.router-link-active {
  color: #fff;
  font-weight: 400;
}
.header {
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  height: 55px;
  background-color: #f60;
}
.nav {
  max-width: 800px;
  margin: 0 auto;
}
.github {
  float: right;
  margin-right: 0;
}
.nav-img {
  line-height: 0;
  vertical-align: middle;
  display: inline-block;
  height: 24px;
}
.nav-img img {
    height: 100%;
}
.view {
  max-width: 800px;
  margin: 0 auto;
}



